<template>
<div>
  <div class="container h-screen w-full bg-black bg-opacity-50" style="position:absolute; z-index:1000;padding-top:45vh" v-if="isSearch">
    <vue-loading type="spin" color="#CCCCCC" :size="{ width: '50px', height: '50px' }"></vue-loading>
  </div>
  <div id="index" class="h-screen w-full px-5 text-gray-50">
    <form id="form" novalidate class="">
      <div class="pt-10">
        <label class="block text-md text-gray-00 custom_left" for="cus_name">住所</label>
        <input v-model="area" class="block text-lg w-full pt-3 px-0 mt-0 bg-transparent border-0 border-b-2 appearance-none focus:outline-none focus:ring-0 focus:border-black border-gray-200 custom" id="cus_name" name="cus_name" type="text" required placeholder="東京都渋谷区円山町">
      </div>
      <div class="pt-7">
        <label class="block text-md text-gray-00 custom_left" for="cus_name">築年数（年）</label>
        <input v-model="buildingAge" class="block text-lg w-full pt-3 px-0 mt-0 bg-transparent border-0 border-b-2 appearance-none focus:outline-none focus:ring-0 focus:border-black border-gray-200 custom" id="cus_name" name="cus_name" type="number" required placeholder="16">
      </div>
      <div class="pt-7">
        <label class="block text-md text-gray-00 custom_left" for="cus_name">階数</label>
        <input v-model="floorNumber" class="block text-lg w-full pt-3 px-0 mt-0 bg-transparent border-0 border-b-2 appearance-none focus:outline-none focus:ring-0 focus:border-black border-gray-200 custom" id="cus_name" name="cus_name" type="number" required placeholder="3">
      </div>
      <div class="pt-7">
        <label class="block text-md text-gray-00 custom_left" for="cus_name">広さ(m²)</label>
        <input v-model="occupiedArea" class="block text-lg w-full pt-3 px-0 mt-0 bg-transparent border-0 border-b-2 appearance-none focus:outline-none focus:ring-0 focus:border-black border-gray-200 custom" id="cus_name" name="cus_name" type="number" required placeholder="25.4">
      </div>
      <div class="pt-7">
        <label class="block text-md text-gray-00 custom_left" for="cus_name">管理費（円）</label>
        <input v-model="costs" class="block text-lg w-full pt-3 px-0 mt-0 bg-transparent border-0 border-b-2 appearance-none focus:outline-none focus:ring-0 focus:border-black border-gray-200 custom" id="cus_name" name="cus_name" type="number" required placeholder="30000">
      </div>
    </form>
    <button
      class="mt-20 inline-block w-full px-6 py-2 text-xs font-medium leading-6 text-center text-white uppercase transition bg-gradient-to-r from-indigo-600 to-indigo-400 rounded-full shadow ripple hover:shadow-lg focus:outline-none"
      @click="onSearch"
    >
      検索
    </button>
  </div>
</div>
</template>

<script>
import { VueLoading } from 'vue-loading-template'

export default {
  name: 'index',
  data () {
    return {
      isSearch: false,
      area: undefined,
      buildingAge: undefined,
      floorNumber: undefined,
      occupiedArea: undefined,
      costs: undefined
    }
  },
  components: {
    VueLoading
  },
  methods: {
    async onSearch () {
      this.isSearch = true
      const param = {
        buildingAge: Number(this.buildingAge),
        costs: Number(this.costs),
        floorNumber: Number(this.floorNumber),
        area: this.area,
        occupiedArea: Number(this.occupiedArea)
      }
      const reponse = await this.$store.dispatch('searchAreaPrice', param)
      if (reponse.status === 200) {
        this.$router.push('/Complate')
      } else {
        alert('エラーが発生しました。入力をお確かめの上もう一度お試しください。')
      }
    }
  }
}
</script>

<style scoped>
#index{
  background: #1B222B;
  text-align: right;
}
.custom::placeholder {
  text-align: right;
}
.custom{
  text-align: right;
}
.custom_left{
  text-align: left;
}
</style>
