import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    price: 0
  },
  getters: {
    // eslint-disable-next-line no-unused-vars
    price (state, getters) {
      return state.price
    },
  },
  mutations: {
    setPrice (state, payload) {
      const price = Math.round(payload * 10000)
      state.price = price
    }
  },
  actions: {
    // eslint-disable-next-line no-unused-vars
    async searchAreaPrice (context, params) {
      const param = params
      const response = await axios.post('https://obite.azurewebsites.net/api/v1/fushigidane/home/price/area/query', param)
      context.commit('setPrice', response.data)
      return response
    }
  },
  modules: {
  }
})
