import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'
import Complate from '../views/Complate.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Index',
    component: Index
  },
  {
    path: '/Complate',
    name: 'Complate',
    component: Complate
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
