<template>
  <div id="complate" class="h-screen w-full text-gray-50">
    <section class="h-20">
      <div class="px-1 pt-1">
        <!-- <svg xmlns="http://www.w3.org/2000/svg" @click="onBack" height="30px" viewBox="0 0 24 24" width="30px" fill="#FFFFFF"><path d="M0 0h24v24H0V0z" fill="none"/><path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z"/></svg> -->
      </div>
    </section>
    <section class="px-5">
      <div class="flex justify-center">
        <div>
          <img class="rounded-full h-24 w-24 bg-gray-100" src="https://placehold.jp/150x150.png" />
        </div>
      </div>
      <div class="flex justify-center">
        <div>
          サービス名
        </div>
      </div>
      <div class="mt-10 flex justify-center">
        <div>
          AI 予想相場
        </div>
      </div>
      <div class="flex justify-center">
        <div class="text-7xl">
          &yen;<ICountUp
            :delay="1000"
            :endVal="money"
            :options="options"
            @ready="onReady"
            class=""
          />
        </div>
      </div>
      <button
        class="mt-10 inline-block w-full px-6 py-2 text-md font-medium leading-6 text-center text-gray-300 transition bg-transparent border-2 border-gray-300 rounded-full ripple focus:outline-none"
        @click="goHome"
      >
        Topに戻る
      </button>
    </section>
  </div>
</template>

<script>
import ICountUp from 'vue-countup-v2'
import { mapGetters } from 'vuex'

export default {
  name: 'complate',
  data () {
    return {
      endVal: 0,
      money: 0,
      options: {
        useEasing: true,
        useGrouping: true,
        separator: ',',
        decimal: '.',
        prefix: '',
        suffix: ''
      }
    }
  },
  components: {
    ICountUp
  },
  computed: {
    ...mapGetters({
      price :'price'
    })
  },
  mounted () {
    this.money = this.price
  },
  methods: {
    // eslint-disable-next-line no-unused-vars
    onReady (instance, CountUp) {
      instance.update(this.money)
    },
    onBack () {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push('/')
    },
    goHome () {
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>
#complate{
  background: #1B222B;
}
</style>
